<template>
  <v-app>
  <v-content>
  <v-container style="max-width: 800px;">
    <h1 class="text-center">T KHAI IN T</h1>
    <h5 class="text-center">(Dng cho cng dn Vit Nam  ngh cp, sa i h chiu ph thng  trong nc)</h5>
    <v-form
      ref="form"
      v-model="valid"
      lazy-validation
    >
      <v-row justify="space-between">
        <v-col md="7">
          <v-text-field
            autocomplete="on"
            v-model="hoten"
            :counter="10"
            :rules="nameRules"
            label="1. H v tn"
            required
          ></v-text-field>
        </v-col>
        <v-col md="4">
          <v-radio-group v-model="gioitinh" row label="2. Gii tnh">
            <v-radio label="Nam" value="male"></v-radio>
            <v-radio label="N" value="female"></v-radio>
          </v-radio-group>
        </v-col>
      </v-row>

      <v-row justify="space-between">
        <v-col md="3">
          <v-menu
            v-model="menu1"
            :close-on-content-click="false"
            max-width="290"
          >
            <template v-slot:activator="{ on }">
              <v-text-field
                :value="computedDateFormattedMomentjs"
                clearable
                label="3. Ngy sinh"
                readonly
                v-on="on"
                @click:clear="ngaysinh = null"
              ></v-text-field>
            </template>
            <v-date-picker
              v-model="ngaysinh"
              @change="menu1 = false"
            ></v-date-picker>
          </v-menu>
        </v-col>
        <v-col md=3>
          <v-select
            :items="items"
            :rules="[v => !!v || 'Vui lng chn ni sinh']"
            required
            label="Ni sinh"
            v-model="noisinh"
            outlined
            dense
          ></v-select>
        </v-col>
      </v-row>

      <v-row justify="space-between">
        <v-col md="4">
          <v-text-field
            v-model="cmnd"
            :rules="[
              v => !!v || 'Bn cha nhp s CMND',
              v => (v && v.length == 9 || v.length == 12 ) || 'S CMND phi c hn 9 hoc 12 s',
            ]"
            label="4. S CMND"
            required
          ></v-text-field>
        </v-col>

        <v-col md="3">
          <v-menu
            v-model="menu2"
            :close-on-content-click="false"
            max-width="290"
          >
            <template v-slot:activator="{ on }">
              <v-text-field
                :value="computedDateFormattedMomentjs2"
                clearable
                label="Ngy cp"
                readonly
                v-on="on"
                @click:clear="ngaycap = null"
              ></v-text-field>
            </template>
            <v-date-picker
              v-model="ngaycap"
              @change="menu2 = false"
            ></v-date-picker>
          </v-menu>
        </v-col>

        <v-col cols="12" sm="3">
          <v-select
            :items="items"
            :rules="[v => !!v || 'Vui lng chn ni cp']"
            v-model="noicap"
            required
            label="Ni cp"
            outlined
            dense
          ></v-select>
        </v-col>
      </v-row>

      <v-row justify="space-between">
        <v-col md="3">
          <v-text-field
            v-model="sodienthoai"
            :rules="[
              v => !!v || 'Bn cha nhp s S in thoi',
              v => (v && v.length == 10 ) || 'S in thoi phi c 10 s',
            ]"
            label="7. S in thoi"
            required
          ></v-text-field>
        </v-col>
        <v-col md="5">
          <v-text-field
            v-model="email"
            :rules="emailRules"
            label="Email"
            required
          ></v-text-field>
        </v-col>
      </v-row>

      <v-row justify="space-between">
        <v-col md="3">
          <v-select
            :items="items"
            :rules="[v => !!v || 'Vui lng chn dn tc']"
            v-model="dantoc"
            required
            label="5. Dn tc"
            outlined
            dense
          ></v-select>
        </v-col>
        <v-col md="3">
          <v-select
            :items="items"
            label="6. Tn gio"
            :rules="[v => !!v || 'Vui lng chn tn gio']"
            v-model="tongiao"
            required
            outlined
            dense
          ></v-select>
        </v-col>
      </v-row>

      <v-row justify="space-between">
        <v-col md="5">
          <v-select
            :items="items"
            :rules="[v => !!v || 'Vui lng chn tnh/TP thng tr']"
            v-model="dcthuongtru"
            required
            label="8. a ch thng tr"
            outlined
            dense
          ></v-select>
        </v-col>
        <v-col md="4">
          <v-select
            :items="items"
            :rules="[v => !!v || 'Vui lng chn qun huyn thng tr']"
            v-model="quanhuyen"
            required
            label="Qun huyn"
            outlined
            dense
          ></v-select>
        </v-col>
      </v-row>

      <v-row justify="space-between">
        <v-col>
          <v-text-field
            v-model="dcchitiet"
            label="a ch chi tit"
          ></v-text-field>
        </v-col>
      </v-row>

      <v-row
        align="center"
        justify="center"
      >
        <v-btn
          :disabled="!valid"
          color="primary"
          class="mr-4"
          @click="apply"
        >
          NP H S
        </v-btn>
      </v-row>
      <v-row>
        <br>
      </v-row>
      <v-row
        align="center"
        justify="center"
      >
      </v-row>
    </v-form>
  </v-container>
  </v-content>
  </v-app>
</template>

<script>
  import moment from 'moment'
  import PassportServices from '@/services/PassportServices'

  export default {
    data: () => ({
      hoten: "",
      gioitinh: "",
      noisinh: "",
      cmnd: "",
      noicap: "",
      sodienthoai: "",
      dantoc: "",
      tongiao: "",
      dcthuongtru: "",
      quanhuyen: "",
      dcchitiet: "",
      ngaysinh: new Date().toISOString().substr(0, 10),
      ngaycap: new Date().toISOString().substr(0, 10),
      menu1: false,
      menu2: false,
      valid: true,
      nameRules: [
        v => !!v || 'Vui lng nhp h tn',
        v => (v && v.length > 5) || 'H v tn phi ln 5 k t',
      ],
      email: '',
      emailRules: [
        v => !!v || 'Vui lng nhp email',
        v => /.+@.+\..+/.test(v) || 'Email khng ng nh dng',
      ],
      select: null,
      items: [
        'Item 1',
        'Item 2',
        'Item 3',
        'Item 4',
      ],
      checkbox: false,
    }),
    computed: {
      computedDateFormattedMomentjs () {
        return this.ngaysinh ? moment(this.ngaysinh).format('DD/MM/YYYY') : ''
      },
      computedDateFormattedMomentjs2 () {
        return this.ngaycap ? moment(this.ngaycap).format('DD/MM/YYYY') : ''
      },
    },
    methods: {
      async apply () {
        await PassportServices.apply({
          hoten: this.hoten,
          gioitinh: this.gioitinh,
          ngaysinh: this.ngaysinh,
          noisinh: this.noisinh,
          cmnd: parseInt(this.cmnd),
          ngaycap: this.ngaycap,
          noicap: this.noicap,
          sodienthoai: this.sodienthoai,
          email: this.email,
          dantoc: this.dantoc,
          tongiao: this.tongiao,
          dcthuongtru: this.dcthuongtru,
          quanhuyen: this.quanhuyen,
          dcchitiet: this.dcchitiet
        })
      },
      validate () {
        this.$refs.form.validate()
      }
    }
  }
</script>